<template>
  <div>
      <h3>Add Todo</h3>
      <div class="add">
        <form @submit='onSubmit'>
         <input type="text" v-model='title' placeholder="Add Todo">
         <input type="submit" value='Add'>
         </form>
      </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
   name:'AddTodo',
   data() {
       return{
           title:''
       }
   },
   methods: {
       ...mapActions(['addTodo']),
       onSubmit(e) {
           e.preventDefault();
           this.addTodo(this.title)
       }
   }
   
}
</script>

<style scoped>
  form {
      display: flex;
  }
  input[type='text'] {
      flex:10;
      padding: 10px;
      border:1px solid #412;
      outline: 0;
  }
  input[type='submit'] {
      flex: 2;
      background-color:#412 ;
      color:aliceblue;
      font-weight: 900;
      cursor:pointer
  } 
</style>